	module.exports = {"data":{"site":{"meta":{"title":"Code Note","description":"Siheng's personal blog","url":"https://sihengwang.github.io/","author":"Siheng","adsense":""}},"post":{"id":"/Users/siheng/dev/personal/blogger/src/pages/articles/2018-05-12-example/index.md absPath of file >>> MarkdownRemark","html":"<h1>An h1 header</h1>\n<p>Paragraphs are separated by a blank line.</p>\n<p>2nd paragraph. <em>Italic</em>, <strong>bold</strong>, and <code class=\"language-text\">monospace</code>. Itemized lists\nlook like:</p>\n<ul>\n<li>this one</li>\n<li>that one</li>\n<li>the other one</li>\n</ul>\n<!--more-->\n<h2>An h2 header</h2>\n<p>content starts at 4-columns in.</p>\n<blockquote>\n<p>Block quotes are\nwritten like so.</p>\n<p>They can span multiple paragraphs,\nif you like.</p>\n</blockquote>\n<p>Use 3 dashes for an em-dash. Use 2 dashes for ranges (ex., “it’s all\nin chapters 12—14”). Three dots … will be converted to an ellipsis.\nUnicode is supported. ☺</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// javascript</span>\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"Hello World!!\"</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>","frontmatter":{"layout":"post","title":"Markdown Example","path":"/markdown-example/","categories":["Markdown"],"date":"2018/05/12","description":null}}},"pathContext":{"path":"/markdown-example/"}}

